
//index.js
import myLoading from './loading.vue';
export default {
    /* 
    * Vue:Vue 
    * options:
    */
    install(Vue, options) {
        /*
        *Vue.extend https://cn.vuejs.org/v2/api/#Vue-extend
        * Vue.extend  (Loading)(myLoading) 
        *  Loading  Profile HTMLElement
        */
        const Loading = Vue.extend(myLoading);
        const Profile = new Loading({
            el: document.createElement('div')
        });

        /*
        *el https://cn.vuejs.org/v2/api/#el
        *loading.vuetemplate *Profile.$eltemplate
        */

        // document.body 
        document.body.appendChild(Profile.$el);

        // icon progressColor 
        if (options) {
            if (options.icon)
                Profile.icon = options.icon;
            if (options.progressColor)
                Profile.progressColor = options.progressColor;
        }
        //  open text 
        const myLoadingMethod = {
            open(text) {
                Profile.show = true;
                if (text)
                    Profile.text = text;
            },
            hide() {
                Profile.show = false;
            }
        };
        // Vue,
        Vue.prototype.$myLoading = myLoadingMethod;
    }
}



