
import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)


/**
 * vuexstate,getter,actions,mutations
 * state
 * gettergettervuex
 * mutations Vuex  store  mutation
 */
const store = new Vuex.Store({
    state: {
        // 
        api: 'http://localhost:3000',
        // 
        user_img: '',
    },
})

export default{
    store
}


